import dict from './../json/dictionary.json';
import { startFight } from './fight.js';
import classChange from './class_change.js';
import { ENTER } from './index.js';

let arrTranslate;
let $answerTranslate = $('#answer-translate');

let taskTranslate = () => {
    arrTranslate = dict[_.random(0, dict.length - 1)];
    classChange($('.task-translate'));
    $('#word').html(arrTranslate.word);
}

let translateCheck = () => {
    if ($answerTranslate.val().trim().length > 0 && arrTranslate.translate.some(a => a === $answerTranslate.val().trim().toLowerCase())) {
        startFight(true, 10);
    } else {
        startFight(false);
    }
}

$('#btn-translate').click(translateCheck);

$answerTranslate.keydown((e) => {
    if (e.keyCode === ENTER) {
        translateCheck();
    }
});

export default taskTranslate;
