let startAnimation = (pers, ctx) => {

    let animate = () => {
        requestAnimationFrame(animate);
        ctx.clearRect(0, 0, canvas.width, canvas.height);
        pers.forEach(a => {
            a[0].update();
            a[0].draw(a[1], a[2]);
        });
    }
    animate();
}

export default startAnimation;
