import taskNumber from './task_number.js';
import taskTranslat from './task_translat.js';
import taskVoice from './task_voice.js';
import taskSwap from './task_swap.js';

let choiceTask = () => {

    $('#btn-attack').click(() => {
        $('.magic-container').removeClass('scale');
        $('#btn-attack').addClass('hide');
    });

    $('.magic-container').click((e) => {
        if (e.target.className == 'btn-fire') {
            taskNumber();
        } else if (e.target.className == 'btn-air') {
            taskTranslat();
        } else if (e.target.className == 'btn-water') {
            taskSwap();
        } else if (e.target.className == 'btn-earth') {
            taskVoice();
        }
    });
}

export default choiceTask;
