import { ENTER, SPACE } from './index.js';

let preDefault = () => {
    let active = true;

    $('input').focus(() => {
        active = false;
    });

    $('input').blur(() => {
        active = true;
    });

    $(document).keydown(e => {
        if (e.keyCode === ENTER || e.keyCode === SPACE) {
            if (active) {
                e.preventDefault();
            }
        }
    });
}

export default preDefault;
