let audio = new Audio();

let soundFight = (evilSound, sound, sts) => {
    if (sound === 0) {
        audio.src = './app/audio/Socapex - Swordsmall.wav';
    } else {
        audio.src = evilSound;
    }

    if (sts) {
        audio.play();
        audio.loop = true;
    } else {
        audio.pause();
        audio.currentTime = 0.0;
    }
}

export default soundFight;
