import gameOver from './game_over.js';
import { numWin } from './game_win.js';

let timer;

let changeOfTime = (sec) => {
    timer = setInterval(() => {

        if (sec < 0) {
            clearInterval(timer);
            gameOver(numWin);
        } else {
            $('.timer').html(sec);
            sec -= 1;
        }

    }, 1000);
}

let timeGame = (sec) => {
    clearInterval(timer);
    changeOfTime(sec);
}

export { timeGame, timer };
