import Count from './Count.js';
import { startFight } from './fight.js';
import classChange from './class_change.js';
import { ENTER } from './index.js';

let arr = [];
let $answerNumber = $('#answer-number');

let taskNumber = () => {
    let count = new Count(1, 100);
    classChange($('.task-math'));
    arr = count.arithmetic();
    $('#task').html(arr[0]);
}

let numberCheck = () => {
    if ($answerNumber.val().trim().length > 0 && $answerNumber.val() === arr[1]) {
        startFight(true, 15);
    } else {
        startFight(false);
    }
}

$('#btn-number').click(numberCheck);

$answerNumber.keydown((e) => {
    if (e.keyCode === ENTER) {
        numberCheck();
    }
});

export default taskNumber;
