import DrawImg from './Draw.js';
import evilArr from './../json/evil.json';
import soundFight from './sound_fight.js';
import startAnimation from './animation.js';

let canvas = document.querySelector('#canvas2');
let context = canvas.getContext('2d');
let size;
let pos;
let indArr;

let canDraw = (speedHero, startHero, endHero, posHero, speedEvil, startEvil, endEvil, posEvil, nxt, sts, sound) => {

  if (nxt) {
    indArr = _.random(0, 7);
  }

  soundFight(evilArr[indArr][1], sound, sts);

  if (indArr > 1) {
    size = 410;
    posEvil = posEvil - 50;
    pos = 143;

    if (indArr === 7) {
      posEvil = 670;
    }
  } else {
    size = 522;
    pos = 163;
  }

  let hero = new DrawImg('./app/images/hero/hero.png', context, 631, 579, speedHero, startHero, endHero, 300);
  let evil = new DrawImg(evilArr[indArr][0], context, 522, 522, speedEvil, startEvil, endEvil, size);
  let persArr = [[hero, posHero, 240], [evil, posEvil, pos]];

  startAnimation(persArr, context);
}

export default canDraw;
