import classChange from './class_change.js';
import 'webpack-jquery-ui/sortable';
import dict from './../json/dictionary.json';
import sortArr from './sort';
import { startFight } from './fight.js';

let strSwap = '';
let sortStrSwap;
let $sort = $("#sort");

let taskSwap = () => {
    let word = '';
    strSwap = dict[_.random(0, dict.length - 1)].word;
    sortStrSwap = strSwap.split('');
    sortArr(sortStrSwap);
    classChange($('.task-swap'));

    _.forEach(sortStrSwap, (a) => {
        word += `<li class="ui-state-default letter">${a}</li>`;
        $sort.html(word);
    })
}

$sort.sortable({
    containment: '.task-swap'
});
$sort.disableSelection();

$('#btn-swap').click(() => {
    if ($('.letter').text() === strSwap) {
        startFight(true, 15);
    } else {
        startFight(false);
    }
});

export default taskSwap;
