import Name from './Name.js';
import nameArr from './../json/name.json';
import { ESC } from './index.js';
import locStProgress from './save.js';
import canDraw from './draw_pers.js';
import { healthRestore } from './fight.js';

let numWin = 0;
let gameWin = () => {
    $('#btn-attack').css('display', 'none');

    setTimeout(() => {
        let nameEvil = new Name(nameArr[0], nameArr[1], nameArr[2]);
        healthRestore();
        $('.m-win').addClass('hide');
        canDraw(6, 0, 11, 50, 6, 0, 9, 720, true);
        $('#name-evil').html(nameEvil.generatorName());
        $('#btn-attack').removeAttr('style');
    }, 1500);

    $('.m-win').removeClass('hide');
    numWin += 1;
    $('#win').html(numWin);
}

$(document).keydown((e) => {
    if (e.keyCode === ESC) {
        locStProgress(numWin);
        window.location.reload();
    }
});

export { gameWin, numWin };
