import { timeGame } from './timer.js';
import positionBattle from './battle.js';
import { healthRecoveryHero, restoringHealthCharacters } from './health.js';

let healthHero = 100;
let healthEvil = 100;

let healthRestore = (hero) => {
    timeGame(60);
    $('.timer').removeClass('timer-up');

    if (hero) {
        if ((healthHero + hero) > 100) {
            healthRecoveryHero(100);
            healthHero = 100;
        } else {
            healthRecoveryHero(healthHero += hero);
        }
    } else {
        healthHero = healthEvil = restoringHealthCharacters();
    }

}

let startFight = (val, damage) => {
    timeGame(60);

    if (val) {
        positionBattle(true, '#prog-evil', '#evil-num', healthEvil -= damage);
    } else {
        positionBattle(false, '#prog-hero', '#hero-num', healthHero -= 25);
    }

}

export { startFight, healthRestore, healthEvil, healthHero };
