class Name {
    constructor(oneName, twoName, threeName) {
        this.oneName = oneName;
        this.twoName = twoName;
        this.threeName = threeName;
    }

    generatorName() {
        let i = _.random(0, this.oneName.length - 1);
        let j = _.random(0, this.twoName.length - 1);
        let k = _.random(0, this.threeName.length - 1);

        return `${this.oneName[i]} ${this.twoName[j]} ${this.threeName[k]}`;
    }
}

export default Name;
