import speakVoice from './voice.js';
import dict from './../json/dictionary.json';
import { startFight, healthRestore } from './fight.js';
import classChange from './class_change.js';
import { ENTER } from './index.js';

let strVoice = '';
let $txtVoice = $('#txt-voice');

let taskVoice = () => {
    strVoice = dict[_.random(0, dict.length - 1)].word;
    speakVoice(strVoice);
    classChange($('.task-voice'));
}

let voiceCheck = () => {
    if ($txtVoice.val().trim().length > 0 && $txtVoice.val().trim().toLowerCase() === strVoice) {
        healthRestore(20);
    } else {
        startFight(false);
    }
}

$('#btn-answer-voice').click(voiceCheck);

$txtVoice.keydown((e) => {
    if (e.keyCode === ENTER) {
        voiceCheck();
    }
});

export default taskVoice;
